import Vuex from 'vuex'
import { createStore } from 'vuex'
import auth from './auth'

export default createStore({
  state: {
    test: null,
    headerLight:true,
    alertText: "",
    showAlert: false,
    alertType: "warning",
  },
  mutations: {
    darkHeader (state) {
      state.headerLight = false;
  },
    lightHeader (state) {
      state.headerLight = true;
  },
    alert (state, data) {
      if(data.text !== undefined) {
        state.alertText = data.text;
      } else {
        state.alertText = data;
      }
      if(data.type !== undefined) {
        state.alertType = data.type;
      }
      state.showAlert = true;
      setTimeout(function(){
        state.showAlert = false;
      }, 1600)
    }
  },
  actions: {

  },
  modules: {
    auth
  }
})
