<script>

import axios from 'axios';
import {mapActions} from 'vuex';

export default {
  name: 'templateHeader',
  data() {
      return {
          displayToken: null,
      }
  },
  methods : {
      ...mapActions({
          signIn: 'auth/signIn',
          login:"auth/login"
      }),
      testMetamask(){

          if (typeof window.ethereum !== 'undefined') {
             this.signIn();
          }
          else {
              this.$store.commit("alert", "Metamask extension not available, please install/enable it.");
          }
      },
      logoutMetamask(){
            this.$store.commit("auth/logout");
            this.$router.push({ name: "Home"});
      }
  },
  computed: {
    headerLight () {
      return this.$store.state.headerLight
    }
  },
}
</script>

