<template>
  <div class="no-bottom no-top" id="content">
    <div id="top"></div>

    <!-- section begin -->
    <section
      id="subheader"
      class="text-light"
      data-bgimage="url(images/background/subheader.jpg) top"
    >
      <div class="center-y relative text-center">
        <div class="container">
          <div class="row">
            <div class="col-md-12 text-center">
              <h1>Profile</h1>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
    </section>
    <!-- section close -->

    <!-- section begin -->
    <section aria-label="section">
      <div class="container">
        <div class="row wow fadeIn">
          <div class="col-lg-7 offset-lg-1">
            <form
              id="form-create-item"
              class="form-border"
              method="post"
              action="email.php"
            >
              <div class="field-set">
                <h5>Upload Profile Picture</h5>
                <UploadImages @changed="handleImages" :max="1" id="profile-pic" class="d-create-file"/>
                <div class="spacer-10"></div>
                <!-- <div v-if="errorPic">{{errorPic}}</div> -->
                 <transition name="fadeAlert">
                    <div :class="'alert-'+$store.state.alertType" class="alert " role="alert" v-if="$store.state.showAlert">
                        {{$store.state.alertText}}
                    </div>
                  </transition>
                <div v-if="successPic">{{successPic}}</div>
                <div class="de_tab tab_methods">
                  <div class="de_tab_content">
                    <div id="tab_opt_1">
                      <h5>Username</h5>
                      <input
                        type="text"
                        name="user_name"
                        id="user_name"
                        v-model="userName"
                        class="form-control"
                        placeholder="enter username"
                      />
                    </div>
                  </div>
                </div>

                <h5>BIO</h5>
                <div class="bio_area">
                <textarea
                  data-autoresize
                  name="biography"
                  id="biography"
                  v-model="bio"
                  class="form-control"
                  placeholder="Tell us about yourself"
                ></textarea>
                <div v-if="bio.length > 1" class="bio_area_delete" @click="deleteBio"><img src="../../public/images/ui/fi-rr-cross-small.svg" ></div>
                </div>
                <div class="spacer-10"></div>
                <div v-if="successInfo">{{successInfo}}</div>
                <div class="spacer-10"></div>
                <div class="spacer-10"></div>
                <div class="spacer-10"></div>

                <input
                  type="button"
                  id="submit"
                  class="btn-main"
                  value="Update Profile"
                  @click="updateProfile"
                />
              </div>
            </form>
          </div>

          <div class="col-lg-3 col-sm-6 col-xs-12">
            <h5>Socials</h5>
            <div id="socials">
              <h5>Instagram</h5>
                <div class="insta-area">
                  <input
                    type="text"
                    v-model="insta"
                    name="instagram"
                    id="instagram"
                    class="form-control"
                    placeholder="enter instagram"
                  />
                  <div v-if="insta" class="insta-area_delete" @click="deleteSocial('instagram')"><img src="../../public/images/ui/fi-rr-cross-small.svg" ></div>
                </div>
              <div class="spacer-single"></div>
              <h5>Twitter</h5>
                <div class="twitt-area">
                  <input
                    type="text"
                    name="twitter"
                    v-model="twitt"
                    id="twitter"
                    class="form-control"
                    placeholder="enter twitter"
                  />
                  <div v-if="twitt" class="twit-area_delete" @click="deleteSocial('twitter')"><img src="../../public/images/ui/fi-rr-cross-small.svg" ></div>
                </div>
            </div>
            <div class="spacer-10"></div>
          <div v-if="successSocials">{{successSocials}}</div>
          </div>
        </div>
      </div>
      <transition name="fadeAlert">
        <div :class="'alert-'+$store.state.alertType" class="alert " role="alert" v-if="$store.state.showAlert">
          {{$store.state.alertText}}
        </div>
      </transition>
    </section>
  </div>
</template>

<script>

import axios from "axios";
import UploadImages from "vue-upload-drop-images"

export default {
    components: {
      UploadImages,
    },
    data: function() {
        return {
          userName: "",
          bio: "",
          twitt: "",
          insta: "",
          image: null,
          message: "",
          successPic: "",
          successInfo: "",
          successSocials: "",
          errorPic: ""
        }
    },
    computed: {
        Username : function(){
          return this.$store.state.auth.username
        },
        Biography : function(){
          return this.$store.state.auth.biography
        },
        twitter : function(){
          return this.$store.state.auth.twitter
        },
        instagram : function(){
          return this.$store.state.auth.instagram
        },
    },
    mounted() {
        this.$store.commit("darkHeader");
        this.userName = this.$store.state.auth.username;
        this.bio =  this.$store.state.auth.biography;
        this.twitt = this.$store.state.auth.twitter.username;
        this.insta = this.$store.state.auth.instagram.username;
        document.getElementsByClassName('mainMessage')[0].innerHTML = "PNG, JPG or GIF, Max 2MB & 500x500px "; 
    },
    unmounted() {
        this.$store.commit("lightHeader");
    },
    methods: {
        updateProfile(){
          if(this.userName !== this.$store.state.auth.username || this.bio !== this.$store.state.auth.biography){
                  axios.patch('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress, 
                  {
                    username: this.userName,
                    bio: this.bio,
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
                    this.successInfo = "Profile info has been successfully updated"
                    setTimeout(() => {
                      this.successInfo = "";
                    }, 2000)   
                    this.updateSucceed(response.status)
                  })
          }

          if (this.$store.state.auth.twitter.username && this.$store.state.auth.twitter.username.length > 0 && this.twitt !== this.$store.state.auth.twitter.username){
            axios.patch('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials/" + this.$store.state.auth.twitter.id, 
                  {
                    name: "Twitter",
                    username: this.twitt
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
                    this.successSocials = "Socials have been successfully updated"
                    setTimeout(() => {
                      this.successSocials = "";
                    }, 2000)
                     this.updateSucceed(response.status)                   
                })
          }else if(!this.$store.state.auth.twitter.username && this.twitt) {
           axios.put('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials" , 
                  {
                    name: "Twitter",
                    username: this.twitt
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
                                        this.$store.dispatch("auth/getUserInfo");
                    this.successSocials = "Socials have been successfully updated"
                    setTimeout(() => {
                      this.successSocials = "";
                    }, 2000)
                     this.updateSucceed(response.status)     
             })
          };
          if (this.$store.state.auth.instagram.username && this.$store.state.auth.instagram.username.length > 0 && this.insta !== this.$store.state.auth.instagram.username){
            axios.patch('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials/" + this.$store.state.auth.instagram.id, 
                  {
                    name: "Instagram",
                    username: this.insta
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
                    this.successSocials = "Socials have been successfully updated"
                    setTimeout(() => {
                      this.successSocials = "";
                    }, 2000)     
                    this.updateSucceed(response.status)
                    this.$store.dispatch("auth/getUserInfo");
                    this.successSocials = "Socials have been successfully updated"
                    setTimeout(() => {
                      this.successSocials = "";
                    }, 2000)
                     this.updateSucceed(response.status)   
             })
          }else if(!this.$store.state.auth.instagram.username && this.insta) {
           axios.put('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials" , 
                  {
                    name: "Instagram",
                    username: this.insta
                  })
                  .then((response) => {
                    this.$store.dispatch("auth/getUserInfo");
                    this.$store.dispatch("auth/getUserInfo");
                    this.successSocials = "Socials have been successfully updated"
                    setTimeout(() => {
                      this.successSocials = "";
                    }, 2000)
                     this.updateSucceed(response.status)   
             })
          }
          if (this.image){
            var formData = new FormData();
            formData.append("image", this.image)
            axios.post('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/picture", 
                  formData, {
                    headers: {
                      'Content-Type': 'multipart/form-data'
                    }
                  })
                  .then((response) => {
                    this.successPic = "Profile picture has been successfully updated"
                    setTimeout(() => {
                      this.successPic = "";
                    }, 2000);
                    this.$store.commit("auth/updateProfilePic");
                    this.updateSucceed(response.status)
             })
          }
        },

         deleteBio() {
           this.bio = " ";
         },

         updateSucceed(status) {
           if(status === 200) {
              this.$store.commit('alert', {"text": "Profile updated successfully", "type" : "success"});
           }   
         },

         deleteSocial(social) {
           if(this.$store.state.auth[social].id){
            axios.delete('https://api.nftslab.io/api/v1/users/' + this.$store.state.auth.publicAddress + "/socials" + "/" + this.$store.state.auth[social].id)
              .then((response) => {
              this.$store.dispatch("auth/getUserInfo");
            });
           }
            if(social === "instagram") {
              this.$store.commit("auth/deleteInstagram");
              this.insta = "";
            }
            else if( social === "twitter") {
              this.$store.commit("auth/deleteTwitter");
              this.twitt = ""
            }
          },

         handleImages(files){ 
           if(files[0]) {
              if(files[0].size < 2097152 && (files[0].type !== "image/png" ||  files[0].type !== "image/jpeg" || files[0].type !== "image/gif") ){
                this.image = files[0];
                // this.errorPic = ""
              }
              else {
                this.$store.commit("alert", {"text": "Image must be PNG, JPEG or GIF and max 2MB", "type" : "danger"})
                // this.errorPic = "Image must be PNG, JPEG or GIF and max 2MB"
                document.querySelector(".imgsPreview .clearButton").click();
              }
          }
         }
    }
};
</script>

<style>

.imgsPreview .imageHolder .plus{
  display: none !important;
}

#form-create-item .field-set .container {
  border: dashed 3px #dddddd ;
  background-color: #fff;
}

.beforeUpload .icon {
  filter: invert(40%) sepia(85%) saturate(1791%) hue-rotate(230deg) brightness(93%) contrast(89%);
}

.bio_area{
  position: relative;
 
}

.bio_area textarea {
  padding-right: 35px;
}

.bio_area_delete img{
  width: 24px;
  height: 100%;
}
.bio_area_delete{
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%)
}
#form-create-item textarea {
  margin-bottom: 0;
}

.insta-area {
  position: relative;
}

.insta-area_delete{
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%)
}
.insta-area_delete img {
    width: 24px;
    height: 100%;
}
.twitt-area {
  position: relative;
}
.twit-area_delete{
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%)
}
.twit-area_delete img {
    width: 24px;
    height: 100%;
}
</style>