<template>
  <templateHeader  />

  <router-view/>

  <templateFooter />
</template>

<script>
import templateHeader from '@/components/header.vue'
import templateFooter from '@/components/footer.vue'
import axios from 'axios'

export default {
  components: {
    templateHeader,
    templateFooter,
  },
  data() {
      return {
          token: null
      }
  },
  mounted() {
    if(localStorage.user){
      this.$store.commit("auth/updateUser", JSON.parse(localStorage.user));
      this.$store.commit('auth/updateProfilePic');
                
    }
  },
  beforeUnmount() {
      localStorage.user = JSON.stringify(this.$store.state.auth)
  },
  methods : {

  },

}

</script>

